div.note {
  height: 150px;
  width: 145px;
  margin-top: 4em;
  background-color: whitesmoke;
  float: left;
  margin-left: 2em;
  position: relative;
  cursor: -webkit-grab;
  box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, .2);
}

div.note:active {
  cursor: -webkit-grabbing;
}

div.note p {
  font-size: 22px;
  padding: 5px;
  font-family: "Shadows Into Light", Arial;
}

div.note:hover > span {
  opacity: 1;
}

div.note > span {
  position: absolute;
  bottom: 2px;
  right: 2px;
  opacity: 0;
  -webkit-transition: opacity .25s linear;
  transition: opacity .25s linear;
}

div.note button {
  margin: 2px;
}

div.note > textarea {
  height: 75%;
  background: rgba(255, 255, 255, .5);
}

div.board button {
  color: #fff;
  font-size: 14px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.55);


}
div.board button:hover {
  opacity: 0.8;
}
div.board button:focus {
outline: none;

}

#add {
  background-color: slategray;
  height: 3em;
  position: absolute;
  left: 0;
}
#add span {
  font-size: 16px;

}
#edit {
  background-color: #337ab7;
}

#save {
  background-color: #5cb85c;
}

#remove {
  background-color: #d9534f;
}

